import React from "react";
import "../Styles/clickygame.css";
import pictures from "./pictures.json";

const maxCount = 12;
let topScore = 0;
class Clickygameboard extends React.Component {
    state = {
        count: 0,
        topScore: 0,
        message: "",
        pictures
    };


    checkClicks = (event) => {

        //id is returned as a string, so need to convert to integer to determine which picture was clicked
        let id = parseInt(event.target.id);

        //pictures will be the array that gets updated based on if a picture was clicked 

        let pictures = [...this.state.pictures];
        let message = "";
        let count = 0;
        //check to see if picture previously clicked - if so, reset counter and pictures clicked
        if (pictures[id].clicked) {
            message = "You Lose, Try Again";
            count = 0;
            pictures.map(item => (
                item.clicked = false
            ));
        }
        else {

            //first time picture clicked, check if all pictures have been clicked and user is a winner
            //if winner, reset counter and pictures clicked
            pictures[id].clicked = true;
            count = this.state.count + 1;
            if (count === maxCount) {
                message = "Congratulations - You Win!";
                topScore = maxCount;
                count = 0;
                pictures.map(item => (
                    item.clicked = false
                ));
            }
            else {
                message = "";
            }
        };
        //shuffle the pictures after a picture has been clicked

        var shuffled = pictures.map((a) => [Math.random(), a]).sort((a, b) => a[0] - b[0]).map((a) => a[1]);
        pictures = shuffled;

        //check to see if user has new top score
        if (topScore < count) {
            topScore = count;
        }
        this.setState({ count: count, pictures: pictures, message: message, topScore: topScore }); // creates a NEW state object

    };
    //renders game page
    render() {
        return (
            <div className="container">
                <h2 className="card-text" id="msgformat">{this.state.message}</h2>
                <h4 id="score">Score: {this.state.count}/Top Score: {this.state.topScore}</h4>
                <div className="card-body">
                    <div className="row">
                        {this.state.pictures.map((item, index) => (
                            <div className="col-md-4" id="imgBorder" key={item.id}>
                                <img src={item.image} alt="" width={215} height={215} id={index} onClick={this.checkClicks}></img>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        );
    }
}

export default Clickygameboard;
