import React from "react";
import "../Styles/pictureformat.css";
import "../Styles/headerformat.css";

function Title() {
    return (
        <div className="container container-fluid">
            <div className="card-header text-white center" id="bgTitle">

                <div className="text-center">
                    <h1 className="headerformat">Clicky Game</h1>
                    <h2>Test Your Memory</h2>
                    <h5>Click on an image to earn points, but don't click on any image more than once!</h5>
                </div>
            </div>
        </div>
    )};

export default Title;
